import service from '../../service.js'
export default{
	data(){
		return{
			show:'',
			user:'',
			
		}
	},
	methods:{
		quit(){
			uni.reLaunch({
				url:'../logo/logo'
			})
		},
		jump(url,data){
			
			uni.navigateTo({
				url:url
			})
		}
		
	},
	onShow() {
		this.user = uni.getStorageSync('user')
		service.entire(this,service.api_root.user.realname,{token:uni.getStorageSync('token')},function(self,res){
			if(res.data = false) self.show = true
		})
	}
}
